namespace Bosco


#if HTML5
open Fable.Core
open Fable.Import
open Fable.Import.Browser
open Fable.Core.JsInterop
#endif
#if WINDOWS || LINUX
open Microsoft.Xna.Framework
open Microsoft.Xna.Framework.Graphics
#endif

[<AutoOpen>]
module Platform =
    let CreatePoint(x, y) =
        PIXI.Point(x, y)

    let CreateSprite(texture) =
        PIXI.Sprite(texture)

    let CreateRect(x, y, w, h) =
        PIXI.Rectangle(x, y, w, h)

